.pagination-box {
  @media screen and (min-width: 768px) {
    width: 768px;
    margin-left: auto;
    margin-right: auto;
    padding-top: 50px;
    padding-bottom: 60px;
  }
  padding-bottom: 60px;
}
.tui-page-bt {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: black;
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
}
.tui-page-bt:hover {
  background-color: #ff6b08;
  border-radius: 5px;
  color: #f7f7f7;
}
.tui-is-selected {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: black;
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  background-color: #ff6b08;
}
.tui-is-selected:focus {
  background-color: #ff6b08;
  border-radius: 5px;
  color: #f7f7f7;
}
.tui-page-btnn {
  display: flex;
  align-items: center;
  justify-content: center;
  color: black;
  width: 40px;
  height: 40px;
}
.tui-page-btnn:hover {
  background-color: #ff6b08;
  border-radius: 5px;
  color: #f7f7f7;
}
.tui-pagination .tui-page-btn {
  background-color: #f7f7f7;
  width: 40px;
  height: 40px;
}
.tui-first {
  background-image: url('data:image/png;base64,');
  background-size: 25px;
  background-repeat: no-repeat;
  background-position: 50%;
}
.tui-last {
  background-image: url('data:image/png;base64,');
  background-size: 25px;
  background-repeat: no-repeat;
  background-position: 50%;
}

.tui-pagination {
  height: 40px;
  display: flex;
  justify-content: center;
  margin: 40px 0 0 0;
}

.tui-pagination .tui-next {
  background-image: url('data:image/png;base64,');
  background-size: 16px;
  background-repeat: no-repeat;
  background-position: 50%;
}

.tui-pagination .tui-prev {
  background-image: url('data:image/png;base64,');
  background-size: 16px;
  background-repeat: no-repeat;
  background-position: 50%;
}

.tui-pagination .tui-page-btn {
  display: flex;
  border: none;
  align-items: center;
  justify-content: center;
  padding: 0;
  width: 40px;
  height: 40px;
  border-radius: 5px;
  color: black;
  &:hover {
    background-color: rgba(221, 221, 221, 0.473);
  }
}

.tui-pagination .tui-is-selected {
  color: #f7f7f7;
  background: #ff6b08;
  border-color: #ff6b08;
  border-radius: 5px;
}

.tui-pagination .hide-first,
.tui-pagination .hide-last {
  background: #f7f7f7;
  background: rgba(247, 247, 247, 0.5);
}

.tui-pagination .tui-is-selected:hover {
  background: #ff6b08;
}

.tui-pagination .tui-ico-ellip {
  margin-top: 0;
}

.tui-pagination .tui-first-child.tui-is-selected {
  border-left: none;
}

.tui-pagination .tui-ico-ellip,
.tui-pagination .tui-ico-first,
.tui-pagination .tui-ico-last {
  fill: #f7f7f7;
}

.tui-pagination .tui-last-child.tui-is-selected {
  border-right: 1px solid #ff6b08;
}
